import React from 'react'
import './Category.scss'
import { setCategory } from '../../../redux/actions/actions'
import { useSelector, useDispatch } from 'react-redux'

function Category({ name, index }) {
    const dispatch = useDispatch()
    const activeCategory = useSelector(
        ({ activeCategory }) => activeCategory.activeCategory
    )

    function setCategoryHandler(index) {
        dispatch(setCategory(index))
    }
    return (
        <div
            className={activeCategory === index 
                ? 'Category Active'
                : 'Category'}
            onClick={() => {
                setCategoryHandler(index)
            }}
        >
            <div className='Category-Name'>{name}</div>
        </div>
    )
}

export default Category
