import React from 'react'
import { addItem, removeItem } from '../../redux/actions/actions'
import { useDispatch, useSelector } from 'react-redux'

function CartItem({ item }) {
    const dispatch = useDispatch()
    const product = useSelector(({ products }) =>
        products[item.category].filter(obj => obj.id === item.id)
    )

    return (
        <div className='CartItem'>
            <div className='CartItem-Img'>
                <img src={item.imageUrl} alt='img'></img>
            </div>
            <div className='CartItem-Title'>{item.name}</div>
            <div className='CartItem-TotalPrice'>
                Total cost: {item.totalPrice}$
            </div>
            <button
                className='CartItem-Add CartItem-Btn'
                onClick={() => {
                    dispatch(addItem(product[0]))
                }}
            >
                Add
            </button>
            <div className='CartItem-Quantity'>{item.quantity}</div>
            <button
                className='CartItem-Remove CartItem-Btn'
                onClick={() => {
                    dispatch(removeItem(product[0]))
                }}
            >
                Remove
            </button>
        </div>
    )
}
export default CartItem
