import React, { useState } from 'react'
import './Item.scss'
import { addItem, removeItem } from '../../../redux/actions/actions'
import { useDispatch, useSelector } from 'react-redux'

function Item({ product }) {
    const dispatch = useDispatch()
    const itemsInCart = useSelector(({ cart }) => cart.items[product.id])
    console.log(itemsInCart)

    let [activeSize, setActiveSize] = useState(0)
    const availableSizes = product.sizes

    function onSizeSelectionHandler(index) {
        setActiveSize(index)
    }

    return (
        <div className='Item'>
            {itemsInCart ? (
                <div className='Item-Cart'> {itemsInCart.quantity}</div>
            ) : null}
            <div className='Item-Img'>
                <img src={product.imageUrl} alt='123'></img>
            </div>
            <h3 className='Item-Title'> {product.name}</h3>
            <h3 className='Item-Title'> {product.price + '$'}</h3>
            <div className='Item-Sizes'>
                {availableSizes.map((size, index) => {
                    return (
                        <div
                            key={index}
                            onClick={() => {
                                onSizeSelectionHandler(index)
                            }}
                            className={
                                activeSize === index
                                    ? 'Item-Size Active'
                                    : 'Item-Size'
                            }
                        >
                            {size}
                        </div>
                    )
                })}
            </div>
            <div className='Item-Actions'>
                <button
                    className='Item-Remove Item-Btn'
                    onClick={() => {
                        dispatch(removeItem(product))
                    }}
                >
                    Remove
                </button>
                <button
                    className='Item-Add Item-Btn'
                    onClick={() => {
                        dispatch(addItem(product))
                    }}
                >
                    Add to Cart
                </button>
            </div>
        </div>
    )
}

export default Item
