import React from 'react'
import './Products.scss'
import Item from './Item/Item'
import { useSelector } from 'react-redux'

function Products(props) {
    const products = useSelector(({ products }) => products)

    const currentCateg = useSelector(
        ({ activeCategory }) => activeCategory.activeCategory
    )

    function renderProducts(productCategory) {
        return productCategory.map(product => (
            <Item key={product.id} product={product} />
        ))
    }

    return (
        <div className='Products'>
            {/* Render active category or all */}
            {currentCateg !== null
                ? renderProducts(products[currentCateg])
                : Object.entries(products).map(([_, prod]) =>
                      renderProducts(prod)
                  )}
        </div>
    )
}

export default Products
