const products = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_PRODUCTS':
            return {
                ...state,
                items: action.payload,
                isLoaded: true,
            }
        default:
            return state
    }
}

const initialCategory = {
    activeCategory: null,
}

const activeCategory = (state = initialCategory, action) => {
    switch (action.type) {
        case 'SET_CATEGORY':
            return {
                ...state,
                activeCategory: action.payload,
            }
        default:
            return state
    }
}

const initialCart = {
    items: {},
    totalPrice: 0,
    totalQuantity: 0,
}

const cart = (state = initialCart, action) => {
    switch (action.type) {
        case 'ADD_ITEM': {
            const itemID = action.payload.id
            const itemPrice = action.payload.price
            const totalPrice = state.totalPrice + itemPrice
            const totalQuantity = state.totalQuantity + 1

            if (!state.items[itemID]) {
                return {
                    ...state,
                    totalQuantity: totalQuantity,
                    totalPrice: totalPrice,
                    items: {
                        ...state.items,
                        [itemID]: {
                            quantity: 1,
                            totalPrice: itemPrice,
                            ...action.payload,
                        },
                    },
                }
            } else {
                const itemTotalPrice =
                    state.items[itemID].totalPrice + itemPrice
                const itemQuantity = state.items[itemID].quantity + 1

                return {
                    ...state,
                    totalQuantity: totalQuantity,
                    totalPrice: totalPrice,
                    items: {
                        ...state.items,
                        [itemID]: {
                            quantity: itemQuantity,
                            totalPrice: itemTotalPrice,
                            ...action.payload,
                        },
                    },
                }
            }
        }
        case 'REMOVE_ITEM': {
            const itemID = action.payload.id

            if (!state.items[itemID]) return state

            const itemPrice = action.payload.price
            const totalPrice = state.totalPrice - itemPrice
            const totalQuantity = state.totalQuantity - 1

            if (state.items[itemID].quantity === 1) {
                const newState = { ...state.items }
                delete newState[itemID]
                return {
                    ...state,
                    totalQuantity: totalQuantity,
                    totalPrice: totalPrice,
                    items: { ...newState },
                }
            } else {
                const itemTotalPrice =
                    state.items[itemID].totalPrice - itemPrice
                const itemQuantity = state.items[itemID].quantity - 1

                return {
                    ...state,
                    totalQuantity: totalQuantity,
                    totalPrice: totalPrice,
                    items: {
                        ...state.items,
                        [itemID]: {
                            quantity: itemQuantity,
                            totalPrice: itemTotalPrice,
                            ...action.payload,
                        },
                    },
                }
            }
        }

        default:
            return state
    }
}

const rootReducer = combineReducers({
    products,
    activeCategory,
    cart,
})

export default rootReducer
